<template>
	<div style="padding-bottom: 1.2rem;">
	<fixedheader :title="title"></fixedheader>
	<div class="duihuan">
		<ul>
			<li  v-for="item in items">
				<img :src="item.pic_url"/>
				<div >
					<div>{{item.good_name}}</div>
					<p>100积分 <del>￥{{item.good_price}}</del></p>
					<button class="state1">立即兑换</button>
				</div>
			</li>
		</ul>
		
	</div>
	<div class="fixed-bottom1" @click="duihuanjilu">
			兑换记录
	</div>
	</div>
</template>

<script>

import fixedheader from "../common/FixedHeader"
export default {
    data (){
      return {
      	title:'积分兑换',
          items:[
			
			]
      }
    },
    methods: {
       getJifenGoodsLists(page){
    	this.post('home/Jifen/getJifenShop',{page:page,number:20}).then(function(res){  	
    		if(res.data.code===10000){
    			this.items=res.data.info.data;    		
    			
    			this.items.forEach(function(item){
    				item.pic_url=this.imageURL(item.pic_url)
    				
    			}.bind(this))
    		}
    	}.bind(this))
     },
     duihuanjilu(){
     	this.$router.push('/duihuanjilu')
     }
    },
    mounted(){
      this.getJifenGoodsLists(1)
    },
    components:{fixedheader}
  }	
</script>

<style>
.duihuan{background: #fff;padding-left: .2rem;border-top: 1px #e6e6e6 solid;}
.duihuan li{display: flex;padding: .2rem;padding-left: 0;border-bottom: 1px #e6e6e6 solid;flex-direction: row;align-items: center;}
.duihuan li:last-child{border-bottom: 0;}
.duihuan img{width: 2.4rem;height: 2.4rem;margin-right: .2rem;}
.duihuan p{color: #FF5773;font-size: .3rem;margin: .2rem 0 .25rem;}
.duihuan del{color: #999;font-size: .22rem;}
.duihuan button{border: 0;outline: none;background: #fff;
padding: .16rem .2rem;border-radius: 4px;width:1.7rem;
}
.duihuan .state1{color: #FF5773;border: 1px #FF5773 solid;}
.duihuan .state2{color: #999;border: 1px #999 solid;}

.fixed-bottom1{text-align: center; position: fixed;left:0;right:0; bottom:0;max-width: 750px;margin: auto;color: #fff; width: 100%;height: 1rem;line-height: 1rem; background:#FF5773 ;text-align: center;}
</style>


